import { LimitedOnboardingStatus } from './types';

export enum OnboardingStep {
  StepZero = 'step-zero',
  StepOne = 'step-one',
  Done = 'done',
}

export const stepMap: Record<number, LimitedOnboardingStatus> = {
  0: OnboardingStep.StepZero,
  1: OnboardingStep.StepOne,
  2: OnboardingStep.Done,
};

export const stepMapReverse: Record<LimitedOnboardingStatus, number> = {
  [OnboardingStep.StepZero]: 0,
  [OnboardingStep.StepOne]: 1,
  [OnboardingStep.Done]: 2,
};
