import {
  CookieKey,
  getCookieData,
  setCookieData,
} from '@app/storage/cookies/shared';
import {
  getLocalStorageData,
  LocalStorageKey,
} from '@app/storage/local/shared';

export async function moveUserIdAndDeviceIdFromLocalStorageToCookiesForOldUsers() {
  const userId = await getLocalStorageData(LocalStorageKey.UserId);
  const deviceId = await getLocalStorageData(LocalStorageKey.DeviceId);

  const cookieUserId = await getCookieData(CookieKey.UserId);
  const cookieDeviceId = await getCookieData(CookieKey.DeviceId);

  if (userId && typeof userId === 'string' && !cookieUserId) {
    await setCookieData({ [CookieKey.UserId]: userId });
  }

  if (deviceId && typeof deviceId === 'string' && !cookieDeviceId) {
    await setCookieData({ [CookieKey.DeviceId]: deviceId });
  }
}
